import React, { useState } from 'react';
import Modal from 'react-modal';
import ModalContent from './ModalContent';
import './MenuList.css';
//change   props   state 

export default function MenuList(props) {
  const {name, price, image, introduce, type, menuBasket} = props;
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [modalSend, setModalSend] = useState("");

  function modalSendChange(option) {
    setModalSend(option);
  }
  function openModal() {
    setModalIsOpen(true);
  }

  function closeModal() {
    setModalIsOpen(false);
  }
  //   onClick 
  // <></>  
  return (
    <>
      <figure 
        className="ListBox" 
        onClick={() => {
          openModal();
          for (const [key] of Object.entries(props.price)) {
            // menuBasket.option(key);
            setModalSend(key);
            break;
          }
        }}
      >
        <div className="ListImgWrap">
          <img className="ListImg" src={image} alt={name+""} />
        </div>
        <figcaption className="ListName">
          {name}
        </figcaption>
      </figure>
      <Modal
        className="ModalWrap"
        isOpen={modalIsOpen}
      >
        <section className="ModalBox">
          <ModalContent 
            name={name}
            price={price}
            image={image}
            introduce={introduce}
            type={type}
            menuBasket={menuBasket}
            modalSend={modalSend}
            setModalSend={modalSendChange}
          />
          <section className="ModalButtonWrap">
            {/* <button onClick={closeModal} className="ModalConfirmButton"></button> */}
            <button onClick={() => {
              closeModal(); 
              menuBasket.basket(true);
              }} className="ModalConfirmButton"></button>
            <button onClick={() => {
              closeModal();
              menuBasket.basket(false);
              }} className="ModalConfirmButton"></button>
          </section>
        </section>
      </Modal>
    </>
  )
}