export default function PaymentValue (props) {
  const {price, tabChange0, tabChange1, } = props;
  return (
    <>
      <section className="payingTextWrap">
        <p className="payingText"> { price }</p>
        <p className="payingText">  .</p>
      </section>
      <section className="buttonBox">
        <button className="modalButton" onClick={ tabChange1 }></button>
        <button className="modalButton" onClick={ tabChange0 } ></button>
      </section>
    </>
  )
}
